module.exports = "[![build status](https://secure.travis-ci.org/stephane-monnot/react-vertical-timeline.svg)](http://travis-ci.org/stephane-monnot/react-vertical-timeline) [![bitHound Score](https://www.bithound.io/github/stephane-monnot/react-vertical-timeline/badges/score.svg)](https://www.bithound.io/github/stephane-monnot/react-vertical-timeline) [![Dependency Status](https://david-dm.org/stephane-monnot/react-vertical-timeline.svg)](https://david-dm.org/stephane-monnot/react-vertical-timeline)\n\n# react-vertical-timeline-component - Vertical timeline for React.js\n\n## Install\n\n```code\n$ npm i react-vertical-timeline-component\n```\n\n\n## Usage\n\n```code|lang-jsx\n---\nimport { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';\nimport 'react-vertical-timeline-component/style.min.css';\n\n...\n\n<VerticalTimeline>\n    <VerticalTimelineElement\n      key=\"1\"\n      title=\"Creative Director\"\n      subtitle=\"Miami, FL\"\n      content=\"Creative Direction, User Experience, Visual Design, Project Management, Team Leading\"\n      date=\"2011 - present\"\n      iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}\n      icon={<WorkIcon />}\n    />\n    <VerticalTimelineElement\n      key=\"2\"\n      title=\"Art Director\"\n      subtitle=\"San Francisco, CA\"\n      content=\"Creative Direction, User Experience, Visual Design, SEO, Online Marketing\"\n      date=\"2006 - 2011\"\n      iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}\n      icon={<WorkIcon />}\n    />\n    <VerticalTimelineElement\n      key=\"3\"\n      title=\"Content Marketing for Web, Mobile and Social Media\"\n      subtitle=\"Online Course\"\n      content=\"Strategy, Social Media\"\n      date=\"April 2013\"\n      iconStyle={{ background: 'rgb(233, 30, 99)', color: '#fff' }}\n      icon={<SchoolIcon />}\n    />\n    <VerticalTimelineElement\n      key=\"4\"\n      title=\"Agile Development Scrum Master\"\n      subtitle=\"Certification\"\n      content=\"Creative Direction, User Experience, Visual Design\"\n      date=\"November 2012\"\n      iconStyle={{ background: 'rgb(233, 30, 99)', color: '#fff' }}\n      icon={<SchoolIcon />}\n    />\n    <VerticalTimelineElement\n      key=\"5\"\n      title=\"Bachelor of Science in Interactive Digital Media Visual Imaging\"\n      subtitle=\"Bachelor Degree\"\n      content=\"Creative Direction, Visual Design\"\n      date=\"2002 - 2006\"\n      iconStyle={{ background: 'rgb(233, 30, 99)', color: '#fff' }}\n      icon={<SchoolIcon />}\n    />\n</VerticalTimeline>\n```\n\n## VerticalTimelineElement Props\n\n### `title={ String }`\n\nTitle of the element.\n\n### `subtitle={ String }`\n\nSubtitle of the element.\n\n### `content={ String }`\n\nContent of the element.\n\n### `date={ String }`\n\nDate of the element.\n\n### `iconStyle={ Object }`\n\nStyle of the element's icon.\n\n### `icon={ String }`\n\nIcon of the element.\n\n\n## Showcase\n\n* [My resume (Stéphane Monnot)](https://stephanemonnot.com/).\n\n\n## License\n\n*react-vertical-timeline-component* is available under MIT. See LICENSE for more details.\n\n"

